.hover-line {
  position: relative;
  outline: none;
  transition: 0.3s ease; }
  .hover-line::before {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 50%;
    width: 0;
    height: 2px;
    background-color: #ffffff;
    transition: 0.3s ease; }
    @media (max-width: 767px) {
      .hover-line::before {
        display: none; } }
  .hover-line::after {
    content: "";
    position: absolute;
    bottom: -3px;
    right: 50%;
    width: 0;
    height: 2px;
    background-color: #ffffff;
    transition: 0.3s ease; }
    @media (max-width: 767px) {
      .hover-line::after {
        display: none; } }

.hover-line:hover::before,
.hover-line:focus::before,
.hover-line:hover::after,
.hover-line:focus::after {
  width: 50%; }

.hover-line:active {
  opacity: 0.5; }

